@font-face {
  font-family: "Springville Regular";
  src: url(/sparta-plaesent-demo/static/media/Springville-Regular3.47a715e6.otf);
}

@font-face {
  font-family: "Adobe Caslon Pro";
  src: url(/sparta-plaesent-demo/static/media/ACaslonPro-Regular.07c51e08.otf);
}
/* Header component */
header {
    max-width: 1440px;
    height: 120px;
}
nav {
    padding: 0 140px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    -webkit-align-items: center;
            align-items: center;
}
.nav-logo {
    background-image: url(data:image/png;base64,);
    height: 34px;
    width: 196px;
}
.nav-items {
    display: -webkit-flex;
    display: flex;
    font: bold 10px Arial;
    color: #767676;
    -webkit-align-items: center;
            align-items: center;
}
.nav-items div {
    padding: 0 8px;
    cursor: pointer;
}
.nav-items div:hover {
    opacity: 0.6;
}

/* react-burger-menu style */
.bm-burger-button {
    position: relative;
    height: 10px;
}
.bm-burger-bars {
    background: #767676;
}
/* add style here */
main {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
main > div {
    margin-top: 75px;
    -webkit-column-count: 3;
            column-count: 3;
    grid-column-gap: 60px;
    -webkit-column-gap: 60px;
            column-gap: 60px;
    max-width: 1160px;
}
.main-item {
    display: inline-block;
    margin-bottom: 50px;
    width: 100%;
    border-bottom: 1px solid #767676;
    cursor: pointer;
}

.main-item .date {
    font-family: "Arial", sans-serif;
    font-size: 11px;
    color: #767676;
    margin-top: 40px;
    margin-bottom: 15px;
}
.main-item .title {
    font-family: "Springville Regular", serif;
    font-size: 48px;
    margin-top: 15px;
    margin-bottom: 20px;
    text-decoration: underline;
}
.main-item .presented {
    font-family: "Adobe Caslon Pro", sans-serif;
    font-size: 14px;
    margin-bottom: 50px;
}
footer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 75px 0;
    font-family: "Arial", sans-serif;
    font-size: 11px;
}

footer a:hover {
    color: #767676;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

